<template>
    <v-container fluid class=" fs-14-vw qiong-pt-30">
    
        <qiong-header :space="`qiong-pt-20`">首购推荐</qiong-header>
        
        <qiong-page-panel>
            <bonus-recommend-table-header></bonus-recommend-table-header>
            <div class="qiong-pt-15"></div>
            <bonus-recommend-table :childs="childrens" v-if="childrens != null"></bonus-recommend-table>
            <loading v-else></loading>
        </qiong-page-panel>
    </v-container>
</template>

<script>
import Loading from '../../../components/Data/Loading.vue'
import QiongHeader from '../../../components/Qiong/Pager/QiongHeader.vue'
import QiongPagePanel from '../../../components/Qiong/QiongPagePanel.vue'

import BonusRecommendTable from './Inner/BonusRecommendTable.vue'
import BonusRecommendTableHeader from './Inner/BonusRecommendTableHeader.vue'

    export default {
        components: {
            QiongHeader,
            QiongPagePanel,
                BonusRecommendTable,
                BonusRecommendTableHeader,
                Loading,
        },
        name: '',
        data() {
            return {
                childrens: null
            }
        },
        mounted() {
            this.switchChidren()
        },
        methods: {
            switchChidren() {
                const _this = this
                const many = this.$store.state.users_enroller
                const transfer = this.$store.state.transfer
                if (many) {

                    this.childrens = [ ]
                    api_bonus._build_res(many, transfer, function (u, t) {
                        _this.childrens.push(u)
                    })
                }
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


